body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.calcButton {
  display: block;
  font-size: 35px;
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 450px) {
  .calcButton {
    font-size: 25px;
  }
}

#advancedPanel {
  position: absolute;
  top: 191.5px;
  z-index: -1;
  width: 25%;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  padding: 20px;
  background: lightgreen;
  text-align: center;
  border: 5px solid grey;
  box-sizing: border-box;
}

#advancedPanel h2 {
  margin-top: 0;
}

#advancedButtonGrid {
  display: grid;
  grid-template-columns: 33% 33% 33%;
  grid-template-rows: 25% 25% 25% 25%;
}

@media only screen and (max-width: 1000px) {
  #advancedPanel {
    width: 50%;
    margin-bottom: 30px;
    top: 45.5%;
    left: 25%;
  }
}

#displayBox {
  background: lightblue;
  margin: 0 auto 20px auto;
}

#display {
  background: white;
  padding: 10px;
}

footer {
  margin-top: 50px;
  color: white;
  text-align: center;
  z-index: -10;
}

footer a {
  text-decoration: none;
  color: rgb(230, 55, 55);
  font-weight: bold;
}

footer a:hover {
  text-decoration: underline;
}
html {
  background: black;
}

.App {
  position: relative;
  z-index: 0;
  padding: 25px 0;
}

#calculator {
  box-sizing: border-box;
  position: relative;
  z-index: 1;
  margin: auto;
  width: 35%;
  padding: 25px;
  background: rgb(168, 205, 219);
  border: 5px solid grey;
}

#buttonGrid {
  height: 500px;
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  grid-template-rows: 20% 20% 20% 20% 20%;
}

@media only screen and (max-width: 1000px) {
  #calculator {
    width: 80%;
  }
}

